
"use client";
import { useEffect, useState } from 'react';
import { Box, Flex, Heading, VStack, Text, Tag, Avatar, Spinner, Stack, Button } from '@chakra-ui/react';
import Link from 'next/link';

const statusColumns = [
  { key: 'OPEN', label: 'To Do', color: 'gray' },
  { key: 'IN_PROGRESS', label: 'In Progress', color: 'blue' },
  { key: 'DONE', label: 'Done', color: 'green' },
];

export default function TasksTable() {
  const [tasks, setTasks] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('/api/tasks')
      .then(res => res.json())
      .then(data => {
        // Ensure tasks is always an array
        if (Array.isArray(data)) {
          setTasks(data);
        } else if (data && Array.isArray(data.tasks)) {
          setTasks(data.tasks);
        } else {
          setTasks([]);
        }
        setLoading(false);
      });
  }, []);

  if (loading) return <Flex justify="center" align="center" minH="200px"><Spinner size="lg" /></Flex>;

  return (
    <Flex gap={6} overflowX={{ base: 'auto', md: 'visible' }} py={4}>
      {statusColumns.map(col => (
        <Box key={col.key} bg="#f7f8fa" borderRadius="lg" minW="320px" p={4} boxShadow="md" flex={1}>
          <Heading size="md" mb={4} color={col.color + '.600'}>{col.label}</Heading>
          <VStack align="stretch" spacing={4} minH="80px">
            {tasks.filter((t: any) => t.status === col.key).length === 0 && (
              <Text color="gray.400" fontSize="sm">No tasks</Text>
            )}
            {tasks.filter((t: any) => t.status === col.key).map((t: any) => (
              <Link key={t.id} href={`/tasks/${t.id}`} style={{ textDecoration: 'none' }}>
                <Box bg="#fff" borderRadius="md" p={4} boxShadow="sm" _hover={{ boxShadow: 'lg', bg: '#f0f4ff' }} transition="all 0.2s">
                  <Flex align="center" justify="space-between" mb={2}>
                    <Text fontWeight="bold" fontSize="md">{t.title}</Text>
                    <Tag colorScheme={col.color} size="sm">{t.priority}</Tag>
                  </Flex>
                  <Flex align="center" gap={2} fontSize="sm" color="gray.600">
                    <Avatar size="xs" name={t.assigneeName || t.assignedToUserId || 'Unassigned'} />
                    <Text>{t.assigneeName || t.assignedToUserId || 'Unassigned'}</Text>
                    {t.dueDate && <Text ml={2}>Due: {new Date(t.dueDate).toLocaleDateString()}</Text>}
                  </Flex>
                </Box>
              </Link>
            ))}
          </VStack>
        </Box>
      ))}
    </Flex>
  );
}
